<template>
  <div class="content storeAudit">
    <div class="operation">
    <el-form label-width="100px" label-position="right" class="mgt20">
      <el-row>
        <el-col :span="6">
          <el-form-item label="店铺分类">
            <el-select v-model="tempData.categoryName" filterable remote :remote-method="searchCategory" @focus="searchCategory('')">
              <el-option v-for="item in categoryList" :key="item.categoryCode" :label="item.categoryName"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="店铺分分区">
            <el-select v-model="tempData.areaName" filterable clearable remote :remote-method="searchArea" @focus="searchArea('')">
              <el-option v-for="item in areaList" :key="item.areaCode" :value="item.areaCode" :label="item.areaName"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="店铺名称">
            <el-input v-model="tempData.startName"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
          <el-col :span="12">
                <el-form-item label="提交时间" class="mgb0">
                    <el-date-picker
                    type="date"
                    format="yyyy-mm-dd"
                    value-format="yyyy-mm-dd"
                    placeholder="起始时间"
                    v-model="tempData.startTime"
                    ></el-date-picker>
                    <span style="width:60px;">-</span>
                    <el-date-picker
                    type="date"
                    format="yyyy-mm-dd"
                    value-format="yyyy-mm-dd"
                    placeholder="截止时间"
                    v-model="tempData.endTime"
                    ></el-date-picker>
                </el-form-item>
            </el-col>
            <el-col :span="3">
                 <el-form-item class="mgb0"><el-button icon="el-icon-search" type="primary" @click="searchList">查询</el-button></el-form-item>
            </el-col>
      </el-row>
    </el-form>
  </div>
  <div class="table">
    <el-table :data="tableData" v-loading="loading" border >
      <el-table-column type="selection" width="55" align="center"></el-table-column>
      <el-table-column prop="shopName" label="店铺名称" align="center"></el-table-column>
      <el-table-column prop="address" label="店铺地址" align="center"></el-table-column>
      <el-table-column prop="businessTime" label="营业时间" align="center"></el-table-column>
      <el-table-column prop="categoryName" label="店铺分类" align="center"></el-table-column>
      <el-table-column prop="areaName" label="店铺区域名称" align="center"></el-table-column>
      <el-table-column prop="orderPhone" label="联系电话" align="center"></el-table-column>
      <el-table-column label="操作" align="center"><template slot-scope="user"><el-button type="primary" @click="editor(user.row)">详情审核</el-button></template></el-table-column>
    </el-table>
  </div>
    <pagination
      :current-page="page.pageCurrent"
      :page-size="page.pageSize"
      :total="page.pageNum"
      @sizeChange="sizeChange"
      @currentChange="currentChange"
    ></pagination>
    <el-dialog title="店铺审核" :visible.sync="editorVisable"  width="800px" :modal-append-to-body="false" class="editor">
        <el-form :model="editorItem" label-width="80px" label-position="left">
            <el-row :gutter="20">
              <el-col :span="12"><el-form-item label="店铺名称"><el-input disabled :value="editorItem.shopName"></el-input></el-form-item></el-col>
                <el-col :span="12"><el-form-item label="商户ID"><el-input disabled :value="editorItem.id"></el-input></el-form-item></el-col>
            </el-row>
             <el-row :gutter="20">
               <el-col :span="12"><el-form-item label="店铺区域"><el-input disabled :value="editorItem.areaName"></el-input></el-form-item></el-col>
               <el-col :span="12"><el-form-item label="店铺类别"><el-input disabled :value="editorItem.categoryName"></el-input></el-form-item></el-col>
             </el-row>
             <el-form-item label="身份证信息" label-width="100px">
            <el-row :gutter="20" class="flbox">
             <img :src="editorItem.idCardFront" alt="" class="idImg" @click="display(editorItem.idCardFront)"><img :src="editorItem.idCardReverse" alt="" class="idImg" @click="display(editorItem.idCardReverse)">
            </el-row>
             </el-form-item>
           <el-form-item label="营业执照信息" label-width="100px;">
             <el-row class="flbox">
                <img :src="editorItem.businessLicense" alt="" class="businessImg" @click="display(editorItem.idCardFront)">
              </el-row>
           </el-form-item>
           <el-form-item class="flbox">
             <el-button type="success" @click="audit(0)">审核通过</el-button>
             <el-button type="danger" @click="audit(2)">审核不通过</el-button>
           </el-form-item>
        </el-form>
    </el-dialog>
    <el-dialog :visible.sync="displayVisable"  width="800px" :modal-append-to-body="false">
               <img :src="displayUrl" alt="">
    </el-dialog>

  </div>
</template>

<script>
import pagination from "@/components/pagination";
import {store} from "@/api/index"
export default {
  components: {
    pagination
  },
  data() {
    return {
      displayVisable:false,
      displayUrl:null,
      editorVisable:false,
      editorItem:{},
      areaList:[],
      categoryList:[],
      //加载状态
      loading: false,
      //表格数据
      tableData: [],
      //搜索时临时数据
      tempData: {},
      //搜索时用的数据
      searchData: {},
      //分页数据
      page: {
        pageNum: null,
        pageCurrent: 1,
        pageSize: 10
      }
    };
  },
  methods: {
    //审核通过
    audit(val){
            store.Audit({
              id:this.editorItem.id,
              status:val
            }).then(res=>{
                this.editorVisable=false
            })
    },
    //展示图片
    display(url){
          this.displayVisable=true;
          this.displayUrl=url
    },
    //详情审核
    editor(obj){
      this.editorVisable=true
      this.editorItem=obj
    },
    //分区查找
    searchArea(areaName){
      this.$common.throttle(()=>{
          store.storeZoom({
            areaName
          }).then(res=>{
            this.areaList=res.rows
          })
      },1000)
    },
    //查找类别
    searchCategory(val){
            this.$common.throttle(()=>{
              store.storeCategory({
                categoryName:val
              }).then(res=>{
                this.categoryList=res.rows
              })
            },1000)
    },
    initData: function(page, size) {
      this.loading = true;
      this.searchData = this.$common.deepCopy(this.tempData);
      this.searchData.page = this.page.pageCurrent;
      this.searchData.size = this.page.pageSize;
     store.storeAudit(this.searchData).then(res => {
        this.tableData = res.rows;
        this.page.pageNum = res.count;
      });
      setTimeout(() => {
        this.loading = false;
      }, 500);
    },
    searchList: function() {
      this.searchData = this.$common.deepCopy(this.tempData);
      this.page.pageCurrent = 1;
      this.initData(1);
    },
    //页数改变时的回调
    currentChange(val) {
      this.page.pageCurrent = val;
      this.initData();
    },
    //每页总数改变时的回调
    sizeChange(val) {
      this.page.pageSize=val;
      this.page.pageCurrent = 1;
      this.initData(1);
    }
  },
  beforeMount() {
    this.initData();
  }
};
</script>

<style lang="scss">
.businessImg{
  width: 80%;
  height: 300px;
}
.flbox{
  display: flex;
  justify-content: space-between;
}
 .idImg{
    width: 100px;
    height: 100px;
  }
.storeAudit{
  .editor{
    img{
      cursor: pointer;
    }
   .idImg{

      width: 300px;
      height: 300px;
    }
  }
}
</style>




